import { mobileCardFactory, CustomizedBiCardPluginType } from '@ali/4ever-factory';
import locale from '../../../locales/zh_CN';
import createRenderNode from './createRenderNode';
import { BiPluginConfig } from '../../../types';

const createBiCalendarCardPlugin = mobileCardFactory((bizConfig: BiPluginConfig) => {
  const calendarCardConfig = bizConfig.calendarCard || {
    locale: { ...locale.calendarCard.locale },
  };
  const renderNode: CustomizedBiCardPluginType['renderNode'] = createRenderNode(
    { ...calendarCardConfig, isMobile: true },
  );
  return {
    cardType: 'calendar',
    isMobile: true,
    renderNode,
  };
});

export default createBiCalendarCardPlugin;
